/* src/Components/Navbar/Navbar.css */

/* Global styles for the navbar */
.navbar {
    position: fixed;
    top: 0;
    right: 0;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding: 1rem;
  }
  
  .navbar button {
    background-color: #007bff;
    color: black;
    border: none;
    padding: 0.5rem 1rem;
    margin: 0.5rem 0;
    cursor: pointer;
    font-size: 1rem;
    border-radius: 5px;
  }
  
  .navbar button:hover {
    background-color: #0056b3;
  }
  
  /* Desktop styles */
  .desktop-navbar {
    display: none;
  }
  
  .mobile-navbar {
    display: flex;
    justify-content: flex-end;
    width: 100%;
  }
  
  /* Mobile styles */
  .hamburger {
    font-size: 2rem;
    background: none;
    border: none;
    cursor: pointer;
  }
  
  .mobile-menu {
    position: fixed;
    bottom: 2rem;
    right: 1rem;
    background: white;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    overflow: hidden;
    padding: 1rem;
  }
  
  @media (min-width: 768px) {
    /* Desktop layout */
    .desktop-navbar {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
  
    .mobile-navbar {
      display: none;
    }
  }
  
  .navbar {
    border: 2px solid red; /* Add temporary borders for debugging */
  }
  
  .desktop-navbar {
    border: 2px solid blue;
  }
  
  .mobile-navbar {
    border: 2px solid green;
  }
  
  .mobile-menu {
    border: 2px solid orange;
  }
  